import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Pagination from '@material-ui/lab/Pagination';
import {Box} from '@material-ui/core';
import ReactPlayer from 'react-player'
import Divider from '@material-ui/core/Divider';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import { ShareButton } from "react-custom-share";
import { SocialIcon } from 'react-social-icons';
import FacebookIcon from '@material-ui/icons/Facebook';

const useStyles = makeStyles((theme) => ({
    mainFeaturedPost: {
        position: 'relative',
        backgroundColor: theme.palette.grey[800],
        color: theme.palette.common.white,
        marginBottom: theme.spacing(4),
        // backgroundImage: 'url(https://source.unsplash.com/random)',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',

      },
  root: {
    backgroundSize: 'cover',
    marginBottom: theme.spacing(2),
    // maxWidth: 500,
  },
  media: {
    height: 0,
    backgroundSize: 'cover',
    paddingTop: '40.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
}));


export default function MainFeaturedPost(props) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const itemsPerPage = 5;
  const [page, setPage] = React.useState(1);
  const { post } = props;
  const noOfPages = Math.ceil(post.length / itemsPerPage)
  
  console.log(Math.ceil(post.length / itemsPerPage),'ll')

  console.log(noOfPages,'pa')
  const handleChange = (event, value) => {
    console.log(value)
    setPage(value);
  };

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  const shareButtonProps = {
    url: "http://realizestuff.herokuapp.com/",
    network: "Facebook",
    text: "Give it a try - react-custom-share component",
    longtext:
      "Social sharing buttons for React. Use one of the build-in themes or create a custom one from the scratch."
  };
  return (
    <div>
    
        {post
          .slice((page - 1) * itemsPerPage, page * itemsPerPage)
          .map(article => {
            return(
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar} >
         {article.user}
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        title={article.headline}
        subheader={article.date}
      />
      <CardMedia
        className={classes.media}
        image={article.image}
        title="Paella dish"
      />
      <CardContent>
      </CardContent>
      <Accordion>
       <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Read Content </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <h5>Date Published: {article.date}</h5>
            {article.content}
            <Divider />
            
            <Divider />
            <div>
            <ReactPlayer url={article.videofile}
             width='100%'
             height='100%'
             controls = {true}
            />
      </div>         
          </Typography>
        </AccordionDetails>
      </Accordion>
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites">
          <ThumbUpIcon />
        </IconButton>
        <IconButton aria-label="share">
         <ShareButton {...shareButtonProps}>
          <FacebookIcon />
          </ShareButton> 
        </IconButton>
      </CardActions>
    </Card>
    ) })}
    <Divider />
      <Box component="span">
        <Pagination
          count={noOfPages}
          page={page}
          onChange={handleChange}
          defaultPage={1}
          color="primary"
          size="large"
          showFirstButton
          showLastButton
          classes={{ ul: classes.paginator }}
        />
      </Box>
    </div>   
  );
}