import React, { useState } from 'react';
import {
  makeStyles,
  createMuiTheme,
  ThemeProvider
} from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import { Brightness3, WbSunny } from "@material-ui/icons";
import logo from './logo.svg';
import './App.css';
import Blog from './components/Blog'
import { BrowserRouter as Router, Route, Link, Switch,BrowserRouter } from 'react-router-dom';
import SingleFeaturedPost from './components/SingleArticle';
import MainFeaturedPost from './components/MainFeaturedPost'
import SignUp from './components/user/Signup';
import LogIn from './components/user/Login';
import News from './components/News';
import Articles from './components/Articles';
import { CssBaseline, Grid } from "@material-ui/core";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";

const lightTheme = createMuiTheme({
  palette: {
    type: "light"
  }
});

const darkTheme = createMuiTheme({
  palette: {
    type: "dark"
  }
});

const useStyles = makeStyles({
  spacing: {
    marginTop: "50px"
  },
  header: {
    marginTop: "40px",
    fontWeight: 600
  },
  button: {
    color: "orange"
  }
});

function App() {
  const classes = useStyles();
  const [isDark, toggleDark] = useState(true);

  const changeTheme = () => {
    console.log('kjshkj')
    toggleDark(prev => !prev);
  };
  return (
    <ThemeProvider theme={isDark ? darkTheme : lightTheme}>
      <CssBaseline>
        <Container>
      <BrowserRouter>
      <div>
      <Grid container justify="space-between" className={classes.spacing}>
            <Grid item>
        <div>
                <IconButton aria-label="dark" onClick={changeTheme}>
                  {isDark ? (
                    <Brightness3 />
                  ) : (
                    <WbSunny className={classes.button} />
                  )}
                </IconButton>
             
          <Switch>
          <Route path="/signup/"><SignUp /></Route>
          {/* <Route path="/Login/"><LogIn /></Route> */}
          <Route path="/news/"><News /></Route>
          <Route path="/article/"><Articles /></Route>
          {/* <Route path="/article/:id" handler={MainFeaturedPost}><SingleFeaturedPost /></Route>         */}
            <Route path=""><Blog></Blog></Route>
            
        
        </Switch>
        </div>
              </Grid>
              </Grid>
      </div> 
      </BrowserRouter>

    </Container>
      </CssBaseline>
    </ThemeProvider>
  );
}

export default App;
