import React from 'react';
import { withStyles,makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import axios from 'axios';
import Snackbar from '@material-ui/core/Snackbar';


const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);
const useStyles = makeStyles((theme) => ({
    linput: {
        width: "460px"
    },
    button: {
        float: "left"
    }
}));
export default function RequestDemoPopup() {
    const classes = useStyles()
  const [open, setOpen] = React.useState(false);
  const [snakopen, setsnakOpen] = React.useState(false);
  const [values,setValues] = React.useState({
    "first_name": null,
    "last_name": null,
    "email": null,
    "phone": null,
    "message": null
  })

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleInput = (e) =>{
    setValues({
        ...values,
        [e.target.name]:e.target.value
    })
  }
  const handleClosesnackbar = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setsnakOpen(false);
  };
  const handleSubmit =e=>{
      e.preventDefault()
      axios.post('http://realizestuff.herokuapp.com/suggestions/',values).then(response=>{
        console.log(response,'success')
       setOpen(false)
      })
      .catch(error=>{
        setOpen(false)
        setsnakOpen(true)
        console.log(error,'error')
      })
      console.log(values)
  }
  return (
    <div>
      <Button variant="outlined" color="secondary" onClick={handleClickOpen}>
        Suggetions
      </Button>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
         Suggetions
        </DialogTitle>
        <DialogContent>
          We Believe Your Suggetions Help us to Improve Our Performence
     <Grid container spacing={1} direction="row">
      <Grid item sm={5} xs={10}>
      <TextField
            autoFocus
            label="Outlined" variant="outlined"
            margin="dense"
            id="fname"
            name="first_name"
            label="First Name"
            type="text"
            required
            onChange={handleInput}
          />
          </Grid>
          <Grid item sm={5} xs={10}>
      <TextField
            autoFocus
            label="Outlined" variant="outlined"
            margin="dense"
            id="lname"
            name="last_name"
            label="Last Name"
            type="text"
            required
            onChange={handleInput}
          />
          </Grid>
          <Grid item sm={5} xs={10}>
      <TextField
            autoFocus
            label="Outlined" variant="outlined"
            margin="dense"
            id="email"
            name="email"
            label="Email"
            type="email"
            required
            onChange={handleInput}
          />
          </Grid>
          <Grid item sm={5} xs={10}>
      <TextField
            autoFocus
            label="Outlined" variant="outlined"
            margin="dense"
            id="phone"
            name="phone"
            label="Phone"
            type="number"
            onChange={handleInput}
          />
          </Grid>

          <Grid item sm={10} xs={20}>
          <TextField
          className={classes.linput}
          id="outlined-multiline-static"
          label="Your Message"
          margin="dense"
          multiline
          name="message"
          rows={4}
          variant="outlined"
          onChange={handleInput}
        />
          </Grid>
    </Grid>
        </DialogContent>
        <DialogActions>
          <Button variant="outlined" color="secondary" autoFocus onClick={handleSubmit}>
            Submit
          </Button>
        </DialogActions>
      </Dialog>
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        open={snakopen}
        autoHideDuration={2000}
        onClose={handleClosesnackbar}
        message="Error Occured. Something Went Wrong."
        action={
          <React.Fragment>
            <Button color="secondary" size="small" onClick={handleClosesnackbar}>
              UNDO
            </Button>
            <IconButton size="small" aria-label="close" color="inherit" onClick={handleClosesnackbar}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </React.Fragment>
        }
      />
    </div>
  );
}