import Sidebar from './Sidebar';
import Footer from './Footer';
import axios from 'axios'
import InstagramIcon from '@material-ui/icons/Instagram';
import { Divider } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
}));

const sections = [
  { title: 'News', url: '/news/' },
  { title: 'Articles', url: '/article/' },
  // { title: 'Culture', url: '#' },
  // { title: 'Business', url: '#' },
  // { title: 'Politics', url: '#' },
  // { title: 'Opinion', url: '#' },
  // { title: 'Science', url: '#' },
  // { title: 'Health', url: '#' },
  // { title: 'Style', url: '#' },
  // { title: 'Travel', url: '#' },
];

const mainFeaturedPost = {
  title: 'Title of a longer featured blog post',
  description:
    "Multiple lines of text that form the lede, informing new readers quickly and efficiently about what's most interesting in this post's contents.",
  image: 'https://source.unsplash.com/random',
  imgText: 'main image description',
  linkText: 'Continue readingâ¦',
};

const featuredPosts = [
  {
    title: 'Featured post',
    date: 'Nov 12', 
    description:
      'This is a wider card with supporting text below as a natural lead-in to additional content.',
    image: 'https://source.unsplash.com/random',
    imageText: 'Image Text',
  },
  {
    title: 'Post title',
    date: 'Nov 11',
    description:
      'This is a wider card with supporting text below as a natural lead-in to additional content.',
    image: 'https://source.unsplash.com/random',
    imageText: 'Image Text',
  },
];

const posts = [];

const sidebar = {
  title: 'About',
  description:
    'Realize Stuff is a Knowldge sharing platfrom. which we will provide the content of a particular topic briefly.We beleive "Knowledge Is Increases By Sharing But Not By Saving"',
  archives: [
    { title: 'March 2020', url: '#' },
    { title: 'February 2020', url: '#' },
    { title: 'January 2020', url: '#' },
  ],
  
  social: [
    { name: 'Instagram', icon: InstagramIcon, url: '#'},
    { name: 'Twitter', icon: TwitterIcon,url: '#' },
    { name: 'Facebook', icon: FacebookIcon ,url: '#'},
  ],
};

export default function Blog() {
  const classes = useStyles();
  const [Articles, setArticles] = React.useState([]);

  React.useEffect(()=>{
	getArticles()
   },[])
    const getArticles=()=>{
       axios.get('https://realizestuff.herokuapp.com/articles/').then(response=>{
        console.log(response.data)
    setArticles(response.data)}
    )}
  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth="lg">
        <Header title="Realize Stuff" sections={sections} />
        <main>
          <MainFeaturedPost  post={Articles} />
          {/* <CorousalApp /> */}
          <Divider />
           {/* <Grid container spacing={4}>
            {featuredPosts.map((post) => (
              <FeaturedPost key={post.title} post={post} />
            ))}
          </Grid> */}
          <Grid container spacing={5} className={classes.mainGrid}>
            <Main title="" posts={posts} />
            
            <Sidebar
              title={sidebar.title}
              description={sidebar.description}
              archives={sidebar.archives}
              social={sidebar.social}
            />
          </Grid> 
        </main>
      </Container>
      <Footer />
      {/* title="Footer" description="Something here to give the footer a purpose!" */}
    </React.Fragment>
  );
}